.experience {
	width: 100%;
	padding: 6rem 6rem;
	background-color: rgba(255, 255, 255, 0.55);
}

.experience-container {
	max-width: 1140px;
	margin: auto;
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	grid-gap: 40px;
}

.left {
	height: 100%;
	max-width: 100%;
	display: flex;
	flex-direction: column;
	padding: 1rem;
}

.title {
	display: flex;
	font-size: 3rem;
}


.right {
	height: 100%;
	max-width: 100%;
	display: flex;
	flex-direction: column;
	padding: 1rem;
}


h4 {
	font-size: 1.2rem;
	padding-bottom: 0.7rem;
}